import React from 'react';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import Image from '../elements/Image';

const propTypes = {
  ...SectionProps.types
}

const defaultProps = {
  ...SectionProps.defaults
}

const WorktermFourContent = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {

  const outerClasses = classNames(
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container">
        <div className={innerClasses}>
          <div className="center-image-text">
            <div className="reveal-from-bottom" data-reveal-delay="200">
              <Image
                className="round-image"
                src={require('./../../assets/images/job4.png')}
                alt="Features tile icon 01"
                width={64}
                height={64}
              />
            </div>
            <h1 className="pl-16 mt-0 mb-16 reveal-from-bottom" data-reveal-delay="200">
              Vidyard
            </h1>
          </div>
          <div className="text-xxs text-color-primary fw-600 tt-u mb-8 center-content reveal-from-bottom" data-reveal-delay="300">
            Software Developer (Frontend)
          </div>

          <div className="container mb-40">
            <h3 className="mt-8 mb-12 reveal-from-bottom" data-reveal-delay="400">
              A Quick Summary
            </h3>
            <p className="m-0 reveal-from-bottom" data-reveal-delay="400">
              For my fourth work term, I was part of the Create: Make team at Vidyard, a company that was founded in Waterloo (my hometown!) back in May 2010. Vidyards main focus is on business-to-business video marketing and video analytics, however, there are also many non-business-oriented customers that use Vidyards software like teachers, students, and more! Something interesting that I didnt know about Vidyard until recently is that it was founded by a couple of University of Waterloo students and that its main goal was to provide a solution for sharing videos between family and friends. It seems like times have changed though and that the main focus has greatly pivoted! While working at Vidyard, I mainly worked on the Chrome extension, focusing on the video creation flows. Since Vidyard is microservice oriented, I also got to work on countless other products as well, such as the Vidyard desktop application, main dashboard website, mobile apps, and more. The main thing I worked on though was an NPM package that abstracts the blurring of users video backgrounds. With guidance from the team, I got to create this from the ground up! Ive never felt such a high level of ownership in regards to something Ive worked on before, more to come regarding this later on in the report.
            </p>
          </div>
          <div className="container mb-40">
            <h3 className="mt-8 mb-12 reveal-from-bottom" data-reveal-delay="400">
              What I Improved on and Learned About
            </h3>
            <p className="m-0 reveal-from-bottom" data-reveal-delay="400">
              After some reflection at the end of my work term, I realized that I got to learn a crazy amount of new things regarding all aspects of software development! Since the codebase for all of the various applications was hosted on Github, I would say that CIS3760 was pretty helpful since we used Gitlab for my groups class project. CIS3760 also gave me some insight into CI/CD, which I had never worked on during a prior internship. This helped, since at Vidyard I created various integrations through GitHub Actions, such as automating the running of unit tests, linting, and checking NPM package licenses. CIS2750 also helped greatly, since I used JavaScript for the final 2 assignments. At Vidyard, 95% of the code I wrote was either in JavaScript, or TypeScript (which is similar), so I can say without a doubt that this early introduction to frontend development from CIS2750 was invaluable. Im glad I had another chance to work with React since I definitely felt like my skills were getting a bit rusty since my 2nd work term in 2021, which is the last time I used React at the workplace. I got to use Redux too, which was nice, and got to write frontend tests both with Mocha, and with Jest. The main thing I got to work on though during my time at Vidyard, was an NPM package that abstracts the addition of virtual backgrounds to the user's video. This all started with the project getting kicked off in early March, and me getting assigned some shaping tasks. I had never done project shaping before, and since shaping requires a lot of estimated guessing, I felt a bit worried since I didnt want to make poor decisions and cause ripples in the quality of the project. Over time though as I got more comfortable with coming up with estimations, it turned out to not be that bad! After doing some shaping, I was tasked with creating some POCs (proof of concepts) that would add some video background virtualization to any place that Vidyard records. This was quite a difficult task, since each Vidyard product is written by different people, was written at different times, and uses different technologies (some use Vue, some use React). Ultimately, I figured that the best way to deal with this issue would be to send a raw video to the NPM package and pump out a video that has all of the various virtual background effects applied to it. I wanted to ensure that the work I had done was high quality as well, so I also added a linter, some unit tests, and some CI/CD actions, and wrote extensive documentation on Confluence that described project risks, decisions made, code examples, and more. I could go on, but I dont want to make this section too too long, so Ill cut it here.
            </p>
          </div>

          <div className="container mb-40 ">
            <h3 className="mt-8 mb-12 reveal-from-bottom" data-reveal-delay="400">
              My 3 Learning Goals
            </h3>
            <p className="m-0 mb-32 reveal-from-bottom" data-reveal-delay="400">
              Before starting work, I set 3 goals to develop during my work term, as dictated by my Co-op program.
            </p>
            <ol>
              <div className="mb-4 reveal-from-bottom" data-reveal-delay="400"><h5 className="m-0"><li>I want to learn more about microservices, their use cases, and how they interact with each other</li></h5>
                <p>    
                  This was a big one at Vidyard. Throughout all of my other internships, I worked on projects that followed the monolithic approach to software development. Since Vidyard is a pretty mature company with a large user base that is mostly businesses, it has to offer a high level of resilience and flexibility when it comes to its product. Microservices make this easy, since if something goes wrong with some of Vidyards products, issues could be isolated more easily, and available resources could be scaled up. This was proven to me since after participating in some incidents, it turned out that leveraging these options was a common resolution. I also learned that these microservices usually communicate with each other through HTTP/HTTPS, and got to see some of the code that supports this in action. Although microservices provide all of these benefits, I also learned that they may not be the best when it comes to tightly coupled services. While I was at Vidyard, there was a functionality that was turned into a microservice, which was done with an aim of abstracting a common part of the video recording process. However, after several years, a move away from this has been made because the code that constitutes this microservice has gotten increasingly complex and convoluted. I think since I was able to explain all of this, I would say that my goal has been achieved!
                </p>
              </div>
              <div className="mb-4 reveal-from-bottom" data-reveal-delay="400"><h5 className="m-0"><li>I want to further improve my skills with React, and explore some new programming methods that I haven't previously seen</li></h5>
                <p>   
                  Id say that I learned a couple of neat programming tricks during my time at Vidyard. The first one Id like to mention is the usage of the '??' operator, or in other words, the null-coalescing operator. An example of what it looks like in practice is if I have two variables, name and displayName. If name = null, and displayName = name ?? Anonymous. This operator returns the left-hand operand if it isnt null, and if it is, it returns the right hand operator. In the example I gave, it would return Anonymous since the name variable is null. I also discovered the optional chaining operator, or in other words, the '?' operator. This operator allows concise access to properties or methods of objects, since if an attempt to access a property of an undefined object is made, it will no longer throw an error, and instead also just return undefined. This is super helpful, since in the past I would write huge chains of if statements when accessing deeply nested properties. There are countless more cool things I learned about React and JavaScript/TypeScript too, so this goal has been accomplished.
                </p>
              </div>
              <div className="mb-4 reveal-from-bottom" data-reveal-delay="400"><h5 className="m-0"><li>I want to get a better idea of how the product lifecycle functions</li></h5>
                <p>
                  While I was at Vidyard, I got to participate in the entirety of the product lifestyle, from shaping, all the way to release. Originally, my action plan for achieving this goal was quite constricted to pure programming, but after working at Vidyard, I learned that it entailed much more. It would all start with a business case. After a business case was laid out and discussed by senior leadership, the feasibility and timeframe estimates were achieved through epic shaping. I got to participate in this and ended up doing a lot of investigating and performing a fair bit of estimation. The main goal of shaping was to lay out a T-shirt size for each task, a small size would be 1-2 weeks, a medium size would be 3-4 weeks, and a large size would be 6+ weeks. After shaping was done, the team would go back to leadership and confirm if the business case was valuable enough to be pursued. After shaping was done, the building stage would start. I got to collaborate with several other developers, open up some PRs, and test in staging environments throughout. This build phase usually lasted the longest. Near the end of the build phase, I would add LaunchDarkly feature flags, so that the new code could be merged into production, but also selectively activated based on who we wanted to view and use the new feature. After this, the feature would be released internally, then released to a small portion of the free user base, and finally released to 100% of users. All of these steps required close communication with other developers, the project manager, and UI/UX designers. Overall, I would say that I not only accomplished my goal but also learned more than I expected!
                </p>
              </div>
            </ol>
          </div>

          <div className="container mb-40">
            <h3 className="mt-8 mb-12 reveal-from-bottom" data-reveal-delay="400">
              Conclusion
            </h3>
            <p className="m-0 reveal-from-bottom" data-reveal-delay="400">
              Im super happy with all Ive done and everything that Ive experienced throughout my time at Vidyard! I achieved all of my goals and more! I got to work with some key technologies again, including React, Redux, JavaScript, TypeScript, Github, and Jira but also got to work with some new technologies like LaunchDarkly, TensorFlow, Mocha, and Sinon just to name a few. I feel lucky that I was able to have such a high level of ownership over a key project thats used in Vidyards main products, this has been the most impactful and satisfying work Ive ever done for a company. Sometimes I would even find myself working outside of work hours just because of how fun and satisfying it was to do! At this point due to the skills Ive gained regarding product life cycle, I feel like I have enough skills at this point to comfortably lead a work project! Thank you so much for the amazing work term Marcel, Juan, Fernando, Matt, David, Vince, Shaina, Calvin, Justin, and Steve. You guys are undoubtedly key players when it comes to the great culture at Vidyard, and Ill definitely be dropping in every now and then on those Friday afternoon socials :)
            </p>
          </div>
        </div>
      </div>
    </section>
  );
}

WorktermFourContent.propTypes = propTypes;
WorktermFourContent.defaultProps = defaultProps;

export default WorktermFourContent;