import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Image from '../../elements/Image';

const propTypes = {
  data: PropTypes.shape({
    title: PropTypes.string,
    paragraph: PropTypes.string
  }).isRequired,
  children: PropTypes.node,
  tag: PropTypes.oneOf(['h1', 'h2', 'h3'])
}

const defaultProps = {
  children: null,
  tag: 'h2'
}

const SectionHeader = ({
  className,
  data,
  children,
  tag,
  ...props
}) => {

  const classes = classNames(
    'section-header',
    className
  );

  const Component = tag;

  return (
    <>
      {(data.title || data.paragraph) &&
        <div
          {...props}
          className={classes}
        >
          <div className="container-xs">
            {children}
            {data.title &&
              <Component className={
                classNames(
                  'mt-0',
                  data.paragraph ? 'mb-16' : 'mb-0'
                )}>{data.title}</Component>
            }
            <div className="split-item">
              {data.image &&
              <div className="reveal-from-bottom mb-16 split-item-image center-content-mobile reveal-from-bottom mr-100 ml-100">      
                <Image
                  className="round-image"
                  src={require('./../../../assets/images/self2.png')}
                  alt="Picture of Remus"
                  width={350}
                  height={350} 
                />
              </div>
              }
              {data.paragraph &&
                <div className="split-item-content center-content-mobile reveal-from-bottom">
                  <p className="m-0">{data.paragraph}</p>
                </div>
              }
            </div>
          </div>
        </div>
      }
    </>
  );
}

SectionHeader.propTypes = propTypes;
SectionHeader.defaultProps = defaultProps;

export default SectionHeader;